<template>
  <div>
    <!-- Navigation -->
    <nav class="navbar navbar-inverse navbar-fixed-top" role="navigation">
      <div class="container">
        <!-- Brand and toggle get grouped for better mobile display -->
        <div class="navbar-header">
          <button type="button" class="navbar-toggle" data-toggle="collapse" data-target="#bs-example-navbar-collapse-1">
            <span class="sr-only">Toggle navigation</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <router-link class="navbar-brand" to="/books">Ana sayfa</router-link>
        </div>
        <!-- Collect the nav links, forms, and other content for toggling -->
        <div class="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
          <ul class="nav navbar-nav navbar-right">
            <li v-if="user">
              <a href="#" v-on:click="logout">Çıkış</a>
            </li>
            <li v-else>
              <router-link to="/books/login">Giriş</router-link>
            </li>
            <li v-if="user != null">
              <router-link to="/books/profile">{{user.email}}</router-link>

            </li>
            <li class="dropdown">
              <a href="#" class="dropdown-toggle" data-toggle="dropdown">Kitap
                <b class="caret"></b>
              </a>
              <ul class="dropdown-menu">
                <li>
                  <router-link to="/books/write">Kitap ekle</router-link>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <!-- /.navbar-collapse -->
      </div>
      <!-- /.container -->
    </nav>
    <div class="container">
      <!-- Page Heading/Breadcrumbs -->
      <div class="row">
        <div class="col-lg-12">
          <h1 class="page-header">{{title}}
            <small> {{subTitle}} </small>
          </h1>
          <breadcrumb mode="mix"></breadcrumb>
        </div>
      </div>
      <router-view v-on:event="handler"></router-view>
      <!-- Footer -->
      <footer>
        <div class="row">
          <div class="col-lg-12">
            <p>&copy; Kitaplık 2018</p>
          </div>
        </div>
      </footer>
    </div>
  </div>
</template>
<script>
  import firebase from 'firebase'
  export default {
    name:'Root',
    data: function () {
      return {
        title: '',
        subTitle: '',
        user: firebase.auth().currentUser
      }
    },
    methods: {
      handler: function (titles) {
        this.title = titles[0]
        this.subTitle = titles[1]
        //console.log(titles)
      },
      logout: function () {
        firebase.auth().signOut().then(() => {
          this.$router.replace('/books/login')
        })
      }
    }
  }
</script>
<style>
</style>


