<template>
    <div class="col-md-6 center">
        <form class="form-horizontal">
            <fieldset>
                <div class="form-group">
                    <label for="email" class="control-label col-sm-2">Email</label>
                    <div class="col-sm-10">
                        <input type="email" class="form-control" id="email" placeholder="" disabled="" :value="user.email">
                    </div>
                </div>
                <div class="form-group">
                    <label for="password" class="control-label col-sm-2">Yeni şifre</label>
                    <div class="col-sm-10">
                        <input type="password" class="form-control" id="password" placeholder="" v-model="password">
                    </div>
                </div>
                <div class="form-group">
                    <label class="control-label col-sm-2"></label>
                    <div class="text-right col-sm-10">
                        <div id="button1Group" class="btn-group" role="group" aria-label="">
                            <button type="button" id="button1" name="button1" class="btn btn-success" v-on:click="updatePassword">Değiştir</button>
                            <button type="button" id="button2" name="button2" class="btn btn-danger" v-on:click="removeUser">Sil</button>
                        </div>
                    </div>
                </div>
            </fieldset>
        </form>
    </div>
</template>
<script>
    import firebase from 'firebase'
    import toastr from 'toastr'
    export default {
        name: 'ProfilePage',
        data: function () {
            return {
                user: firebase.auth().currentUser,
                password: ''
            }
        },
        methods: {
            // Sayfa başlığını Root şablonuna gönderir
            sendThisPageTitleToRoot: function () {
                this.$emit('event', ['Profil', 'Kullanıcı ayarları'])
            },
            // Şifreyi değiştirme fonk.
            updatePassword: function () {
                this.user.updatePassword(this.password).then(function () {
                    // Update successful.
                    toastr.success("Şifreniz değiştirildi!", "Başarılı", { "positionClass": "toast-bottom-center" })
                }).catch(function (error) {
                    // An error happened.
                    toastr.error(error.message, "Hata", { "positionClass": "toast-bottom-center" })
                })
            },
            // Kullanıcı hesabını kaldırma fonk.
            removeUser: function () {
                toastr.info("<br><button type='button' id='sil' class='btn btn-default'>Evet sil</button>",
                    'Kullanıcı hesabınız silinsin mi?', {
                        closeButton: true,
                        allowHtml: true,
                        positionClass: 'toast-bottom-center',
                        onShown: function () {
                            $("#sil").click(function () {
                                var user = firebase.auth().currentUser
                                user.delete().then(function () {
                                    // User deleted.
                                    toastr.success("Hesabınız silindi!", "Başarılı", { "positionClass": "toast-bottom-center" })
                                    location.href = '/books'
                                }).catch(function (error) {
                                    // An error happened.
                                    toastr.error(error.message, "Hata", { "positionClass": "toast-bottom-center" })
                                });
                            });
                        }
                    })
            }
        },
        mounted() {
            // Sayfa yüklendiğinde fonksiyonu çalıştır
            this.sendThisPageTitleToRoot()
        }
    }
</script>
<style>
</style>


