<template>
    <div class="col-lg-12">
        <form v-on:submit="updateBook" action="" method="POST">
            <fieldset>
                <div class="form-group">
                    <label for="title" class="control-label">Kitap adı</label>
                    <input type="text" class="form-control" id="title" placeholder="" :value="title">
                </div>
                <div class="form-group">
                    <label for="author" class="control-label">Yazar</label>
                    <input type="text" class="form-control" id="author" placeholder="" :value="author">
                </div>
                <div class="form-group">
                    <label for="url" class="control-label">Url</label>
                    <input type="text" class="form-control" id="url" placeholder="" :value="url">
                </div>
                <div class="form-group">
                    <label class="control-label" for="description">Açıklama</label>
                    <textarea class="form-control" id="description" name="description" rows="4">{{description}}</textarea>
                </div>
                <div class="form-group">
                    <label class="control-label" for=""></label>
                    <div class="text-left">
                        <!-- <button type="button" id="" name="" v-on:click="addBook" class="btn btn-primary" aria-label="">Kaydet</button> -->
                        <button type="submit" id="" name="" class="btn btn-primary" aria-label="">Kaydet</button>
                    </div>
                </div>
            </fieldset>
        </form>
    </div>
</template>
<script>
    import firebase from 'firebase'
    import { db } from '../firebase'
    import { booksRef } from '../firebase'
    import toastr from 'toastr'
    export default {
        name: 'Edit',
        data() {
            return {
                title: null,
                author: null,
                url: null,
                description: null
            }
        },
        firebase() {
            return {
                // Kayıtları db den getir
                book: {
                    source: db.ref('/books/' + this.$route.params.id),
                    asObject: true,
                    readyCallback: function () {
                        this.title = this.book.title
                        this.author = this.book.author
                        this.url = this.book.url
                        this.description = this.book.description
                    }
                }
            }
        },
        methods: {
            // Kitap güncelleme fonk.
            updateBook: function (e) {
                var updateRef = booksRef.child(this.$route.params.id)
                updateRef.update({
                    'title': title.value,
                    'author': author.value,
                    'url': url.value,
                    'description': description.value
                }, function (error) {
                    if (error) {
                        toastr.error('Güncelleme başarısız!', { "positionClass": "toast-bottom-center" })
                    } else {
                        toastr.success('Güncelleme başarılı!', { "positionClass": "toast-bottom-center" })
                    }
                })
                e.preventDefault()
            },
            sendThisPageTitleToRoot: function () {
                this.$emit('event', ['Düzenle', 'Kitap kaydını düzenle'])
            }
        },
        mounted() {
            this.sendThisPageTitleToRoot()
        }
    }
</script>
<style>
</style>


