<template>
  <div>
    <!-- Project One -->
    <paginate name="items" :list="books" :per="2" tag="div">
      <div v-for="book in paginated('items')" v-bind:key="book['.key']">
        <div class="row">
          <div class="col-md-5">
              <!-- <img class="img-responsive img-hover" src="http://placehold.it/700x300" alt=""> -->
              <!-- <img class="img-responsive img-hover" src="/static/img/kitaplik-post-img.jpg" alt=""> -->
              <img class="img-responsive img-hover center" v-bind:src="book.url" alt="">
          </div>
          <div class="col-md-7">
            <div class="panel panel-default">
              <div class="panel-heading">
                <router-link v-bind:to="'/books/detail/' + book['.key']"><h3>{{book.title}}</h3></router-link>
              </div>
              <div class="panel-body">
                <h4>{{book.author}}</h4>
                <p> {{ (book.description.substr(0, 400)) }}...
                <router-link v-bind:to="'/books/detail/' + book['.key']">
                  <h4>Devamı...</h4>
                </router-link>
                </p>
              </div>
            </div>
            <div class="panel panel-default">
              <div class="panel-body">
                <span class="glyphicon glyphicon-user" aria-hidden="true"></span> {{book.user}}
                <span class="glyphicon glyphicon-time" aria-hidden="true"></span> {{book.date | formatDate}}
              </div>
            </div>
            <!-- <a class="btn btn-primary" href="portfolio-item.html">View Project</i></a> -->
            <button v-if="userEmail === book.user" class="btn btn-danger" v-on:click="removeBook(book)">
              <span class="glyphicon glyphicon-trash" aria-hidden="true"></span> Sil
            </button>
            <router-link v-if="userEmail === book.user" v-bind:to="'/books/edit/' + book['.key']" class="btn btn-primary">
              <span class="glyphicon glyphicon-edit" aria-hidden="true"></span> Düzenle
            </router-link>
            <br>
            <br>
          </div>
        </div>
        <!-- /.row -->
        <hr>
      </div>
    </paginate>
    <div class="row text-center">
      <paginate-links for="items" :classes="{'ul': 'pagination'}"></paginate-links>
    </div>
  </div>
  <!-- /.row -->
</template>
<script>
  import firebase from 'firebase'
  import { booksRef } from '../firebase';
  import toastr from 'toastr'
  export default {
    name: 'Home',
    data() {
      return {
        user: firebase.auth().currentUser,
        paginate: ['items']
      }
    },
    firebase: {
      books: booksRef
    },
    created() {
    },
    methods: {
      removeBook: function (book) {
        booksRef.child(book['.key']).remove()
        toastr.success('Kitap başarıyla silindi!', 'Başarılı!', { "positionClass": "toast-bottom-center" })
      },
      sendThisPageTitleToRoot: function () {
        this.$emit('event', ['Kitaplık', 'Ana sayfa'])
      }
    },
    mounted() {
      this.sendThisPageTitleToRoot()
    },
    props: {
    },
    computed: {
      userEmail: function () {
        if (this.user) {
          return this.user.email
        } else {
          return false
        }
      },
      formatDate: function () {
        return this.$filters.formatDate
      }
    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>


