<template>
    <div class="col-lg-8 center">
        <hr>
        <p>
            <span class="glyphicon glyphicon-time" aria-hidden="true"></span> {{book.date | formatDate}} tarihinde eklendi.</p>
        <hr>
        <!-- <img class="img-responsive" src="http://placehold.it/900x300" alt=""> -->
        <img class="img-responsive center" v-bind:src="book.url" alt="">
        <hr>
        <p><h3> {{book.title}} </h3></p>
        <p class="lead"> {{book.author}} </p>
        <p> {{book.description}} </p>
        <hr>
    </div>
    </div>
</template>
<script>
    import firebase from 'firebase'
    import { db } from '../firebase'
    import { booksRef } from '../firebase'
    import toastr from 'toastr'
    export default {
        name: 'Detail',
        data() {
            return {
                title: null,
                author: null,
                url: null,
                description: null,
                date: null
            }
        },
        firebase() {
            return {
                // Kayıtları db den getir
                book: {
                    source: db.ref('/books/' + this.$route.params.id),
                    asObject: true,
                    readyCallback: function () {
                        this.title = this.book.title
                        this.author = this.book.author
                        this.url = this.book.url
                        this.description = this.book.description
                        this.date = this.book.date
                    }
                }
            }
        },
        computed: {
            formatDate: function () {
                return this.$filters.formatDate
            }
        }
    }
</script>
<style>
</style>


