<template>
    <div class="col-md-6 center">
        <form>
            <fieldset>
                <!-- Form Name -->
                <!-- <legend>Kayıt ol</legend> -->
                <!-- Text input http://getbootstrap.com/css/#forms -->
                <div class="form-group">
                    <label for="email" class="control-label">Email</label>
                    <input class="form-control" id="email" v-model="email" placeholder="" type="text">
                </div>
                <!-- Text input http://getbootstrap.com/css/#forms -->
                <div class="form-group">
                    <label for="password" class="control-label">Şifre</label>
                    <input class="form-control" id="password" v-model="password" placeholder="" type="password">
                </div>
                <!-- Button http://getbootstrap.com/css/#buttons -->
                <div class="form-group">
                    <label class="control-label" for="login"></label>
                    <div class="text-right">
                        <button type="button" id="login" name="login" v-on:click="signUp" class="btn btn-primary">Kaydet</button>
                    </div>
                    <small> Hesabınız varsa
                        <router-link to="/books/login">giriş</router-link> yapabilirsiniz.
                    </small>
                </div>
            </fieldset>
        </form>
    </div>
</template>
<script>
    import firebase from 'firebase'
    import toastr from 'toastr'
    export default {
        name: 'signUp',
        data: function () {
            return {
                email: '',
                password: ''
            }
        },
        methods: {
            signUp: function () {
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
                    .then((user) => {
                        toastr.success("Kullanıcı kaydedildi!", "Başarılı", { "positionClass": "toast-bottom-center" })
                        this.$router.replace('/books')

                    },
                    (error) => {
                        toastr.error(error.message, "Hata", { "positionClass": "toast-bottom-center" })
                    })
                this.email = ''
                this.password = ''
            },
            sendThisPageTitleToRoot: function () {
                this.$emit('event', ['Kayıt ol', 'Kullanıcı kayıt'])
            }
        },
        created() {
            this.sendThisPageTitleToRoot()
        }
    }
</script>
<style>
</style>


