<template>
    <div class="col-md-6 center">
        <form>
            <fieldset>
                <div class="form-group">
                    <label for="email" class="control-label">Email</label>
                    <input class="form-control" id="email" v-model="email" placeholder="" type="email" required>
                </div>
                <div class="form-group">
                    <label for="password" class="control-label">Şifre</label>
                    <input class="form-control" id="password" v-model="password" placeholder="" type="password" required>
                </div>
                <div class="form-group">
                    <label class="control-label" for="login"></label>
                    <div class="text-right">
                        <button type="button" v-on:click="signIn" class="btn btn-primary" aria-label="">Giriş</button>
                    </div>
                    <small> Bir hesabınız yok mu?
                        <router-link to="/books/sign-up">Buradan</router-link> kayıt olabilirsiniz.
                    </small>
                </div>
            </fieldset>
        </form>
    </div>
</template>
<script>
    import firebase from 'firebase'
    import toastr from 'toastr'
    export default {
        name: 'LoginPage',
        data: function () {
            return {
                email: '',
                password: ''
            }
        },
        methods: {
            signIn: function () {
                firebase.auth().signInWithEmailAndPassword(this.email, this.password)
                    .then((user) => {
                        this.$router.replace('/books')
                    },
                    (err) => {
                        toastr.error(err.message + 'Hata')
                        toastr.options = {
                            "positionClass": "toast-bottom-center"
                        }
                    })
                this.email = ''
                this.password = ''
            },
            sendThisPageTitleToRoot: function () {
                this.$emit('event', ['Giriş', 'Kullanıcı girişi'])
            }
        },
        mounted() {
            this.sendThisPageTitleToRoot()
        }
    }
</script>
<style>
</style>


