import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import SignUp from '@/components/SignUp'
import Write from '@/components/Write'
import Root from '@/components/Root'
import Profile from '@/components/Profile'
import Edit from '@/components/Edit'
import Detail from '@/components/Detail'
import PageNotFound from '@/components/PageNotFound'
import firebase from 'firebase'
Vue.use(Router)
const router = new Router({
    base: '/',
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: {
                path: '/books'
            }
        },
        {
            path: '*',
            component: PageNotFound
        },

        {
            path: '/books',
            component: Root,
            name:'Root',
            meta: {
                breadcrumb: {
                    icon: '',
                    name: 'Ana sayfa'
                }
            },
            children: [
                {
                    path: '',
                    component: Home,
                    name:'Home',
                    meta: {
                        parent: 'Root',
                        breadcrumb: {
                            icon: '',
                            name: ''
                        }
                    }
                },
                {
                    path: 'login',
                    component: Login,
                    name:'LoginPage',
                    meta: {
                        parent: 'Root',
                        breadcrumb: {
                            icon: '',
                            name: 'Giriş yap'
                        }
                    }
                },
                {
                    path: 'sign-up',                    
                    component: SignUp,
                    name:'signUp',
                    meta: {
                        parent: 'Root',
                        breadcrumb: {
                            icon: '',
                            name: 'Kayıt ol'
                        }
                    }
                },
                {
                    path: 'write',                    
                    component: Write,
                    name:'Write',
                    meta: {
                        parent: 'Root',
                        requiresAuth: true,
                        breadcrumb: {
                            icon: '',
                            name: 'Kitap ekle'
                        }
                    }
                },
                {
                    path: 'profile',                    
                    component: Profile,
                    name:'ProfilePage',
                    meta: {
                        parent: 'Root',
                        requiresAuth: true,
                        breadcrumb: {
                            icon: '',
                            name: 'Profil'
                        }
                    }
                },
                {
                    path: 'edit/:id',                    
                    component: Edit,
                    name:'Edit',
                    meta: {
                        parent: 'Root',
                        requiresAuth: true,
                        breadcrumb: {
                            icon: '',
                            name: 'Düzenle'
                        }
                    }
                },
                {
                    path: 'detail/:id',                    
                    component: Detail,
                    name:'Detail',
                    meta: {
                        parent: 'Root',
                        breadcrumb: {
                            icon: '',
                            name: 'Detaylı inceleme'
                        }
                    }
                }
            ]
        }
    ]
})
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        // this route requires auth, check if logged in
        // if not, redirect to login page.
        if (!firebase.auth().currentUser) {
            next('/books/login')
        } else {
            next()
        }
    } else if (firebase.auth().currentUser) {
        next()
    } else {
        next()
    }
})
export default router


