<template>
  <div class="col-lg-12">
    <form v-on:submit="addBook" action="" method="POST">
      <fieldset>
        <!-- Form Name -->
        <!-- <legend>Kitap ekle</legend> -->
        <!-- Text input http://getbootstrap.com/css/#forms -->
        <div class="form-group">
          <label for="title" class="control-label">Kitap adı</label>
          <input type="text" class="form-control" id="title" placeholder="" v-model="newBook.title" required>
        </div>
        <div class="form-group">
          <label for="author" class="control-label">Yazar</label>
          <input type="text" class="form-control" id="author" placeholder="" v-model="newBook.author" required>
        </div>
        <div class="form-group">
          <label for="url" class="control-label">Resim URL (700 x 350)</label>
          <input type="text" class="form-control" id="url" placeholder="" v-model="newBook.url" required>
        </div>
        <!--       <div class="form-group">
        <label for="sel1">Select list:</label>
        <select class="form-control" id="sel1">
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
        </select>
      </div>
 -->
        <!-- Textarea http://getbootstrap.com/css/#textarea -->
        <div class="form-group">
          <label class="control-label" for="description">Açıklama</label>
          <textarea class="form-control" id="description" name="description" rows="4" v-model="newBook.description" required></textarea>
        </div>
        <!-- Button http://getbootstrap.com/css/#buttons -->
        <div class="form-group">
          <label class="control-label" for=""></label>
          <div class="text-left">
            <!-- <button type="button" id="" name="" v-on:click="addBook" class="btn btn-primary" aria-label="">Kaydet</button> -->
            <button type="submit" id="" name="" class="btn btn-primary" aria-label="">Kaydet</button>
          </div>
        </div>
      </fieldset>
    </form>
  </div>
</template>
<script>
  import firebase from 'firebase'
  import { booksRef } from '../firebase';
  import toastr from 'toastr'
  export default {
    name: 'Write',
    data() {
      return {
        newBook: {
          author: null,
          title: null,
          description: null,
          url: null,
          date: Date.now(),
          user: firebase.auth().currentUser.email
        }
      }
    },
    methods: {
      // Kitap ekle fonk.
      addBook: function (e) {
        if (this.newBook.author && this.newBook.title && this.newBook.description && this.newBook.url) {
          booksRef.push(this.newBook)
          this.newBook.title = ''
          this.newBook.author = ''
          this.newBook.url = 'http://'
          this.newBook.description = ''
          toastr.success('Kitap ekleme başarılı!', { "positionClass": "toast-bottom-center" })
          e.preventDefault()
        } else {
          if (!this.newBook.title) toastr.error('Kitap adı gerekli!', 'Hata!', { "positionClass": "toast-bottom-center" })
          if (!this.newBook.author) toastr.error('Yazar gerekli!', 'Hata!', { "positionClass": "toast-bottom-center" })
          if (!this.newBook.url) toastr.error('Url gerekli!', 'Hata!', { "positionClass": "toast-bottom-center" })
          if (!this.newBook.description) toastr.error('Açıklama gerekli!', 'Hata!', { "positionClass": "toast-bottom-center" })
          e.preventDefault()
        }
      },
      sendThisPageTitleToRoot: function () {
        this.$emit('event', ['Kitap ekle', 'Kitap kayıt girişi'])
      }
    },
    mounted() {
      this.sendThisPageTitleToRoot()
    }
  }
</script>
<style>
</style>


